import MainButton from '../../../components/Buttons/MainButton'

interface WorkCardProps {
	key: number
	cardType: string
	data: {
		id: number
		title: string
		text: string
		img: string
		type: string
	}
}

const WorkCard = ({ key, cardType, data }: WorkCardProps) => {
	//region STANDARD CARD
	const standardCard = (
		<div className={`work-card ${cardType === 'big' ? 'work-big-card' : 'work-small-card'} `}>
			<img
				src={data.img}
				alt={data.title}
				className={`card-img ${cardType === 'big' ? 'big-img-h' : 'small-img-h'}`}
			/>
			<div className={`${cardType === 'big' ? '' : ''}`}>
				<p className='card-info-title'>{data.title}</p>
				<p className='card-info-text'>{data.text}</p>
			</div>
		</div>
	)
	//endregion

	// region YOUR CARD
	const yourCard = (
		<div className='your-card-conatiner'>
			<div className={`your-card ${cardType === 'big' ? 'work-big-card' : 'work-small-your-card'} `}>
				<div className={`your-card-img card-img ${cardType === 'big' ? 'big-img-h' : 'small-img-h'}`} />
				<div className={`${cardType === 'big' ? '' : ''}`}>
					<p className='card-info-title'>{data.title}</p>
					<p className='card-info-text'>{data.text}</p>
				</div>
				<div className='lg:flex'>
					<MainButton text='Book a call' />
				</div>
			</div>
		</div>
	)
	//endregion
	return data.type === 'project' ? standardCard : yourCard
}

export default WorkCard
