interface OfferCardProps {
	key: number
	cardData: {
		id: number
		title: string
		text: string
		img: string
		type: string
	}
}

const OfferCard: React.FC<OfferCardProps> = ({ key, cardData }) => {
	//#region Cards
	const StandardCard = (
		<div className='card-type'>
			<div className=''>
				<img
					src={cardData.img}
					alt={cardData.title}
					className='card-type-img'
				/>
			</div>
			<div className=''>
				<p className='card-type-title'>{cardData.title}</p>
				<p className='card-type-text'>{cardData.text}</p>
			</div>
		</div>
	)
	const TemplateCard = (
		<div className='card-template'>
			<div className=''>
				<img
					src={cardData.img}
					alt={cardData.title}
					className='card-template-img'
				/>
			</div>
			<div className=''>
				<p className='card-template-title'>{cardData.title}</p>
				<p className='card-template-text'>{cardData.text}</p>
			</div>
		</div>
	)
	//#endregion
	return cardData.type === 'template' ? TemplateCard : StandardCard
}

export default OfferCard
