import { useDispatch } from 'react-redux';
import { anyActions } from '../../store/any-slice';


const DarkModeToggle = () => {
	const dispatch = useDispatch();
	const handleToggle = () => {
		dispatch(anyActions.toggleDarkMode());
	  };

	return (
		<div className={`ml-12`}>
			<label className='items-center cursor-pointer'>
				<input
					type='checkbox'
					value=''
					className='sr-only peer'
					onChange={handleToggle}
				/>
				<div className="relative w-11 h-6 bg-gray-200 peer-focus:outline-none rounded-full peer dark:bg-gray-300 peer-checked:after:translate-x-full rtl:peer-checked:after:-translate-x-full  after:content-[''] after:absolute after:top-[2px] after:start-[2px] after:bg-black after:rounded-full after:h-5 after:w-5 after:transition-all dark:border-gray-300 peer-checked:bg-gray-300"></div>
			</label>
		</div>
	)
}

export default DarkModeToggle
