import { Link } from 'react-scroll'

import { useState } from 'react'
import DarkModeToggle from './DarkModeToggle'
import classes from './Navbar.module.css'
import SvgCmp from '../SvgCmp/SvgCmp'

const Navbar = () => {
	const [isMenuHidden, setIsMenuHidden] = useState('hidden')

	const closeMenu = () => {
		setIsMenuHidden('hidden')
	}
	const handleMenu = (isMenuOpen: boolean) => {
		if (isMenuOpen) setIsMenuHidden('hidden')
		else isMenuHidden === 'hidden' ? setIsMenuHidden('') : setIsMenuHidden('hidden')
	}
	return (
		<nav className='fixed z-[999] w-screen navbar'>
			<div className={`max-w-screen-xl flex flex-wrap items-center justify-between mx-auto mt-4 ${classes.paddingNav}`}>
				<Link
					to='Home'
					spy={true}
					smooth={true}
					offset={-70}
					duration={500}
					className=''>
					<SvgCmp
						svgName='logo'
						screen='Desktop'
					/>
				</Link>
				<button
					onClick={() => handleMenu(false)}
					data-collapse-toggle='navbar-default'
					type='button'
					className='inline-flex items-center p-2 w-10 h-10 justify-center lg:hidden'
					aria-controls='navbar-default'
					aria-expanded='true'>
					<span className='sr-only'>Open main menu</span>
					<SvgCmp
						svgName='burgerMenu'
						screen='Mobile'
					/>
				</button>
				<div
					className={`${isMenuHidden} w-full lg:flex lg:w-auto max-lg:border max-lg:border-black`}
					id=''>
					<ul className='flex flex-col lg:flex-row gap-6 max-lg:my-6 max-lg:mx-auto max-lg:justify-center max-lg:items-center  lg:gap-8 my-auto text-sm font-medium leading-normal dark-text'>
						<li>
							<Link
								to='Work'
								onClick={closeMenu}
								spy={true}
								smooth={true}
								offset={-70}
								duration={500}
								className='cursor-pointer'>
								Work
							</Link>
						</li>
						<div className=' h-px w-[40%] bg-black'></div>
						<li>
							<Link
								to='How'
								onClick={closeMenu}
								spy={true}
								smooth={true}
								offset={-70}
								duration={500}
								className='cursor-pointer'>
								How
							</Link>
						</li>
						<div className=' h-px w-[40%] bg-black'></div>
						<li>
							<Link
								to='About'
								onClick={closeMenu}
								spy={true}
								smooth={true}
								offset={-70}
								duration={500}
								className='cursor-pointer'>
								About
							</Link>
						</li>
						<div className=' h-px w-[40%] bg-black'></div>
						<li>
							<Link
								to='Contact'
								onClick={closeMenu}
								spy={true}
								smooth={true}
								offset={-70}
								duration={500}
								className='cursor-pointer'>
								Contact
							</Link>
						</li>
					</ul>
					{window.innerWidth > 1024 && <DarkModeToggle />}
				</div>
			</div>
		</nav>
	)
}

export default Navbar
