import SvgCmp from '../SvgCmp/SvgCmp'

import './Badge.scss'

interface BadgeProps {
	text: string
	svg: string
	styling?: string
}

const Badge: React.FC<BadgeProps> = ({ svg, text, styling }) => {
	return (
		<div className={`badge ${styling}`}>
			<SvgCmp
				svgName={svg}
				screen={window.innerWidth < 1024 ? 'Mobile' : 'Desktop'}
				type='badge'
			/>
			<p className='badge-text'>{text}</p>
		</div>
	)
}

export default Badge
