import { ReactNode, useState } from 'react'
import { useSelector } from 'react-redux'
import './MainCmp.css'

const MainCmp = ({ children }: { children: ReactNode }) => {
	const isDark = useSelector((state: { anySlice: { darkMode: boolean } }) => state.anySlice.darkMode)
	return (
		<div
			className='relative scrollbar-hide overflow-clip mainCmp'
			data-theme={isDark ? 'dark' : 'light'}>
			{children}
		</div>
	)
}

export default MainCmp
