import { ReactComponent as Circle } from '../../icons/circle.svg'
import { ReactComponent as CircleMobile } from '../../icons/circle-sm.svg'
import { ReactComponent as ArrowRight } from '../../icons/ArrowRight.svg'
import { ReactComponent as SideArrow } from '../../icons/SideArrow.svg'
import { ReactComponent as BurgerMenu } from '../../icons/burgerMenu.svg'

//*LIGHT MODE ICONS
import { ReactComponent as TitleLightDesktop } from '../../icons/LightMode/health-desktop.svg'
import { ReactComponent as TitleLightmobile } from '../../icons/LightMode/health-mobile.svg'
//* DARK MODE ICONS
import { ReactComponent as TitleDarkDesktop } from '../../icons/DarkMode/health-desktop.svg'
import { ReactComponent as TitleDarkMobile } from '../../icons/DarkMode/health-mobile.svg'

//* default
import { ReactComponent as Duplicate } from '../../icons/Footer/duplicate.svg'
import { ReactComponent as LinkExternal } from '../../icons/icon/link-external.svg'
import { ReactComponent as Health } from '../../icons/icon/health.svg'

//* LOGO
import { ReactComponent as Logo } from '../../icons/Logo/logo.svg'

//*BADGES
import { ReactComponent as bowlHot } from '../../icons/Badge/bowl-hot.svg'
import { ReactComponent as party } from '../../icons/Badge/party.svg'
import { ReactComponent as world } from '../../icons/Badge/world.svg'

import { ReactComponent as ArrowScroll } from '../../icons/icon/chevrons-up.svg'

import './SvgCmp.scss'
import { useSelector } from 'react-redux'
const iconSet = [
	//!LIGHT MODE ICONS
	{ name: 'titleLightDesktop', icon: TitleLightDesktop },
	{ name: 'titleLightMobile', icon: TitleLightmobile },

	//! DARK MODE ICONS
	{ name: 'titleDarkDesktop', icon: TitleDarkDesktop },
	{ name: 'titleDarkMobile', icon: TitleDarkMobile },

	{ name: 'circle', icon: Circle },
	{ name: 'circleMobile', icon: CircleMobile },
	{ name: 'arrowRight', icon: ArrowRight },
	{ name: 'sideArrow', icon: SideArrow },
	{ name: 'burgerMenu', icon: BurgerMenu },
	{ name: 'logo', icon: Logo },
	{ name: 'health', icon: Health },
	{ name: 'bowlHot', icon: bowlHot },
	{ name: 'party', icon: party },
	{ name: 'world', icon: world },
	{ name: 'duplicate', icon: Duplicate },
	{ name: 'linkExternal', icon: LinkExternal },
	{ name: 'arrowScroll', icon: ArrowScroll },
]

//#region STYLES
const bagdeDesktop = 'w-4 h-4'
const bagdeMobile = 'w-3 h-3'
const footerLink = { height: '16px', width: '16px'}
//#endregion

const SvgCmp = (props: { svgName: string; screen: string; type?: string; styles?: string }) => {
	const { svgName, screen, type, styles } = props
	const Icon = iconSet.find(icon => icon.name === svgName)?.icon
	const isDark = useSelector((state: { anySlice: { darkMode: boolean } }) => state.anySlice.darkMode)

	if (!Icon) return null
	else if (type === 'badge') {
		if (screen === 'Desktop') return <Icon className={`${styles !== undefined ? styles : bagdeDesktop}`} />
		else if (screen === 'Mobile') return <Icon className={`${styles !== undefined ? styles : bagdeMobile}`} />
	} else if (type === 'title') {
		if (screen === 'Desktop') return <Icon className={`${styles !== undefined ? styles : 'w-12 h-12'}`} />
		else if (screen === 'Mobile') return <Icon className={`${styles !== undefined ? styles : 'w-6 h-6'}`} />
	} else if (type === 'footer') {
		if (svgName === 'linkExternal')
			return (
				<Icon
					style={footerLink}
					className={`${styles !== undefined ? styles : ''} external${isDark ? '-dark' : ''}`}
				/>
			)
		else if (svgName === 'duplicate')
			return (
				<Icon
					style={footerLink}
					className={`${styles !== undefined ? styles : ''} duplicate`}
				/>
			)
	} else if (screen === 'Desktop') return <Icon className={`${styles !== undefined ? styles : ''}`} />
	else if (screen === 'Mobile') return <Icon className={`${styles !== undefined ? styles : ''}`} />
	return <Icon />
}

export default SvgCmp
