import { useSelector } from 'react-redux'
import SvgCmp from '../SvgCmp/SvgCmp'

import './TitleCmp.scss'

interface TitleCmpProps {
	title: string
}

const TitleCmp: React.FC<TitleCmpProps> = ({ title }) => {
	const isDark = useSelector((state: { anySlice: { darkMode: boolean } }) => state.anySlice.darkMode)

	const titleStyle = isDark ? window.innerWidth < 1024 ? 'titleDarkMobile' : 'titleDarkDesktop' : window.innerWidth < 1024 ? 'titleLightMobile' : 'titleLightDesktop'
	return (
		<div className='flex justify-center items-center gap-2 lg:gap-4'>
			<SvgCmp
				svgName={titleStyle}
				screen={window.innerWidth < 1024 ? 'Mobile' : 'Desktop'}
				type='title'
			/>
			<p className={`title-heading${title.includes('about') ? '-about' : ''}`}>{title}</p>
		</div>
	)
}

export default TitleCmp
