import { createSlice, PayloadAction } from '@reduxjs/toolkit'

interface State {
	type: string
	darkMode: boolean
}

const initialState: State = {
	type: 'home',
	darkMode: false,
}

const anySlice = createSlice({
	name: 'AnySlice',
	initialState: initialState,
	reducers: {
		scrollTo: (state, action: PayloadAction<any>) => {
			console.log(action.payload)
		},
		toggleDarkMode: state => {
			state.darkMode = !state.darkMode // toggle the value
		},
	},
})

export const anyActions = anySlice.actions
export default anySlice.reducer
